import { CLICK_ACTION } from '../lib';
import React from 'react';

export const renderCustomOrDefaultComp = <T extends {}>(
  customRenderer: React.ComponentType<T> | undefined,
  defaultRenderer: React.ComponentType<T>,
  props: T,
): JSX.Element => {
  let ComponentToRender = defaultRenderer;
  if (customRenderer && typeof defaultRenderer === 'function') {
    ComponentToRender = customRenderer;
  }
  return React.createElement(ComponentToRender, props);
};

