export const getLinkParams = ({
  link,
  itemClickAction,
}: {
  link?: { url: string; target?: string };
  itemClickAction?: string;
}): {
  href?: string;
  target?: string;
} => {
  if (itemClickAction === CLICK_ACTION.LINK) {
    const { url, target = '_blank' } = link || {};
    const shouldUseDirectLink = !!url;
    const linkParams = shouldUseDirectLink ? { href: url, target } : {};
    return linkParams;
  } else {
    return {};
  }
};
